import { apolloClient } from "@/providers/apollo.provider";

import type {
  MutationParticipantSignupArgs,
  TransportType,
} from "@/types/graphql";

import ParticipantSignupMutation from "@/graphql/participantSignup.mutation.graphql";

export const participantSignup = async (
  participant: MutationParticipantSignupArgs
): Promise<Array<TransportType>> => {
  return apolloClient
    .mutate({
      mutation: ParticipantSignupMutation,
      variables: participant,
    })
    .then(({ data }) => data.participantSignup);
};
